*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.palette{
  display: grid;
  grid-template-rows: repeat(20,1fr);
  grid-template-columns: 1fr;
  text-align: center;
  position: relative;
  justify-items: center;
  align-items: center;
}
.palette h2{
  position: absolute;
  bottom: 10%;
}

.main{
  display: grid;
  grid-template-columns: repeat(5,1fr);
  height: 100vh;
}
.palette-child{
  width: 100%;
  height: 100%;
}
.toolbars{
  position: absolute;
  top: 30%;
  z-index: 5;
}
.toolbars>ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-gap: 42px;
}
.toolbars ul li{
  cursor: pointer;
}

@media (max-width: 600px) {
  .main{
    grid-template-rows: repeat(5,1fr);
    grid-template-columns: 1fr;
  }
  .palette{
    grid-template-columns: repeat(10,1fr);
    grid-template-rows: 1fr;
  }
  .toolbars>ul{
    display: flex;
    align-items: center;
    grid-gap: initial;
  }
  .toolbars ul li{
    margin: 0 4px;
    grid-gap: 0;
  }
  .palette h2{
    left: 5%;
    bottom: initial;
  }
  .toolbars{
  position: absolute;
  top: initial;
  z-index: 5;
  right: 10%;
  }
}


/* css for tooltip */
.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
    /* width: 100px; */
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 5px 5px;
    border-radius: 6px;
    position: absolute;
    z-index: 1;
    bottom: 100%;
    left: 50%;
    font-size: 14px;
    min-width: 100px;
    margin: 0px 0 8px -50px;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

/* Toast */

.notification-container {
	font-size: 14px;
	box-sizing: border-box;
	position: fixed;
	z-index: 999999
}

.top-right {
	top: 12px;
	right: 12px;
	transition: transform .6s ease-in-out;
	animation: toast-in-right .7s;
}

.bottom-right {
	bottom: 12px;
	right: 12px;
	transition: transform .6s ease-in-out;
	animation: toast-in-right .7s;
}

.top-left {
	top: 12px;
	left: 12px;
	transition: transform .6s ease-in;
	animation: toast-in-left .7s;
}

.bottom-left {
	bottom: 12px;
	left: 12px;
	transition: transform .6s ease-in;
	animation: toast-in-left .7s;
}

.notification {
	background: #fff;
	transition: .3s ease;
	position: relative;
	pointer-events: auto;
	overflow: hidden;
	margin: 0 0 6px;
	padding:8px 16px;
	margin-bottom: 15px;
	width: 300px;
	max-height: 100px;
	border-radius: 3px 3px 3px 3px;
	box-shadow: 0 0 10px #999;
	color: #000;
	opacity: .9;
	background-position: 15px;
	background-repeat: no-repeat;
}

.notification:hover {
	box-shadow: 0 0 4px #fff;
	opacity: 1;
	cursor: pointer
}

.notification-title {
	font-weight: 700;
	font-size: 16px;
	text-align: left;
	margin-top: 0;
	margin-bottom: 6px;
  width: 300px;
  height: 18px;
}

.notification-message {
	margin: 0;
	text-align: left;
  height: 18px;
  margin-left: -1px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.notification-image {
    float: left;
    margin-right: 15px;
}

.notification-image img {
    width: 30px;
    height: 30px;
}

.toast {
  width: 365px;
	color: #fff;
}

.notification-container button {
	position: relative;
	right: -.3em;
	top: -.12em;
	float: right;
	font-weight: 700;
	color: #fff;
	outline: none;
	border: none;
	text-shadow: 0 1px 0 #fff;
	opacity: .8;
	line-height: 1;
	font-size: 16px;
	padding: 0;
	cursor: pointer;
	background: 0 0;
	border: 0
}

@keyframes toast-in-right {
	from {
	  transform: translateX(100%);
	  
	}
	to {
	  transform: translateX(0);
	}
}

@keyframes toast-in-left {
	from {
		transform: translateX(-100%);
		
	}
	to {
		transform: translateX(0);
	}
}